import {Nav, TabContent} from 'react-bootstrap'
import React, {useEffect, useState, useContext} from 'react';
import { useHistory, useParams } from 'react-router-dom'
import {CSSTransition} from 'react-transition-group';
import "./Detail.scss"
import {StockContext} from "./App.js"
import { connect } from 'react-redux';

function Detail(props) {
    let { id } = useParams();
    let histroy = useHistory();
    let [stock, stoctSet] = useState(true);
    let stock2 = useContext(StockContext);
    let [selTab, selTabSet] = useState(0);
    let [tabSwitch, tabSwitchSet] = useState(false);

    useEffect(()=>{
    let alertClose = setTimeout(() => {
        stoctSet(stock=false); }, 2000);
        return ()=>{clearTimeout(alertClose)};
    }, []);//[조건]
    
    return (
        <div className="container">
            { stock === true && props.stock[id] <= 5 ?
            <div className="alertBox">
                <div className="my-alert">
                    <p>재고가 얼마 남지 않았습니다.</p>
                </div>
                <div className="my-alert2">
                    <p>재고 소진시 이벤트 종료</p>
                </div>
            </div>
                : null}
            <div div="div" className="row">
                <div className="col-md-6">
                    <img src={ "https://codingapple1.github.io/shop/shoes"+(Number([id])+1)+".jpg" } width="100%"/>
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{props.data[id].title}</h4>
                    <p>{props.data[id].content}</p>
                    <p className="BeforePrice">{props.data[id].price.toLocaleString()}원</p>
                    <p className="AfterPrice">{(props.data[id].price*0.8).toLocaleString()}원</p>
                    <StockInfo Stock={stock2[id]}></StockInfo>
                    {props.stock[id] > 0 ?
                    <button className="btn btn-danger" onClick={()=>{ 
                        props.dispatch({type : 'AddItem', payload : props.data[id]});
                        histroy.push('/cart');
                    }}
                        >주문하기</button> : null
                    }
                    <button className="btn btn-danger" onClick={()=>{histroy.goBack()}}>뒤로가기</button>
                </div>
            </div>
            <Nav className="mt-5" variant="tabs" defaultActiveKey="link-1">
                <Nav.Item>
                    <Nav.Link eventKey="link-0" onClick={()=>{ tabSwitchSet(false); selTabSet(0);}} >Active</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link-1" onClick={()=>{ tabSwitchSet(false); selTabSet(1);}} >Option 2</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="disabled" disabled>
                    Disabled
                    </Nav.Link>
                </Nav.Item>
            </Nav>

            <CSSTransition in={tabSwitch} classNames="wow" timeout={500}>
                <TabContent selTab={selTab} tabSwitchSet={tabSwitchSet}/>
            </CSSTransition>
        </div>
    )

    function TabContent(props){
        useEffect(()=>{
            props.tabSwitchSet(true);
        })
        if(props.selTab === 0){
            return <div>0번째 내용입니디.</div>
        }else if(props.selTab === 1){
            return <div>1번째 내용입니디.</div>
        }else if(props.selTab === 2){
            return <div>2번째 내용입니디.</div>
        }else {
            return <div>잘못된 선택입니다.</div>
        }
    }

    function StockInfo(props){
        return(
            <p>재고 : { props.Stock }</p>
        )
    }
}

function dataBinding(state) {
    return {  
      cartState : state
    }
  }

export default connect(dataBinding)(Detail);