import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter } from 'react-router-dom';
import {Provider} from 'react-redux'
import { combineReducers, createStore } from 'redux';

let storeCart = [];
let duplicateCheck = false;

function reducer(state = storeCart, CartAction){
  if (CartAction.type === 'IncreasedIn'){
    let copy = [...state];
    copy[CartAction.payload.i].quan++;
    state = copy;
    return state;
  }else if (CartAction.type === 'DecreasedIn'){
    if(state[CartAction.payload.i].quan > 1){
      let copy = [...state];
      copy[CartAction.payload.i].quan--;
      state = copy;
      return state;
    }else {
      return state;
    }
  } else if (CartAction.type === 'DeleteItem'){
    console.log(state[CartAction.payload.i]);
    state.splice(CartAction.payload.i, 1);
    console.log(state);
    return state;
  }else if (CartAction.type === 'AddItem'){
    state.map((e, i)=>{
      if (e.id === CartAction.payload.id){
        duplicateCheck = true;
      }else {
        duplicateCheck = false;
      }
    })
    if(duplicateCheck === false){
      CartAction.payload.quan=1;
      CartAction.payload.discountPrice=CartAction.payload.price*0.8;
      state.push(CartAction.payload);
    }
    return state;
  }else if(CartAction.type === 'Payment'){

  }else if(CartAction.type === 'AllDelete'){
    if(state != null) {
      for(let i=0; i<=state.length;i++ ){
        state.pop();
      }
    }
  }else{
    return state;
  }
}

let store = createStore(reducer);
// let store = createStore(combineReducers({reducer, reducer2})); // 리듀서 여러개 쓰는법

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Provider store={store}>
        <App />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
