import React, { useState } from 'react';
import {Table, Button} from 'react-bootstrap';
import { connect } from 'react-redux';
import { useHistory } from 'react-router-dom';
import "./Cart.scss"

function Cart(props){
  let history = useHistory();
  let [alertSw, alertSwSet] = useState(true);
  let sumPrice = 0;
  return (
    <div className="container"> 
      <Table responsive="sm">
        <thead>
          <tr>
            <th>#</th>
            <th>상품코드</th>
            <th>상품명</th>
            <th>수량</th>
            <th>가격</th>
            <th>변경</th>
          </tr>
        </thead>
        <tbody>
          {props.state != null?
            props.state.map((e, i) => {
              return(
                <tr key={i}>
                  <td>{i}</td>
                  <td>{props.state[i].id}</td>
                  <td><a className="ItemName" onClick={()=>{history.push("/detail/"+props.state[i].id)}}>{props.state[i].title}</a></td>
                  <td>{props.state[i].quan}
                  <Button variant="outline-info" onClick={()=>{props.dispatch({type: 'IncreasedIn', payload : {i}}); }} >+</Button>
                  <Button variant="outline-info" onClick={()=>{props.dispatch({type: 'DecreasedIn', payload : {i}}); }}>-</Button></td>
                  <td>{props.state[i].discountPrice}원</td>
                  <td><Button variant="outline-success" onClick={()=>{props.dispatch({type: 'DeleteItem', payload : {i}}); history.push("/wait"); }}>삭제</Button></td>
                </tr>  
              )
            }) : null
          }
          <tr>
            <th colSpan="4"></th>
            <th>총가격</th>
            {
              props.state != null ?
            props.state.map((e, i)=> {
              console.log(e);
              sumPrice += (e.discountPrice*e.quan);  
            }): null}
            <th>{sumPrice}원</th>
          </tr>
        </tbody>
      </Table>
      {alertSw === true ? 
        <div className="my-alert2 cartAlert">
          <p>행사로 할인이 적용된 가격입니다.</p>
          <button onClick={()=>{alertSwSet(false)}}>닫기</button>
        </div>: null
      }
      <div className="buttonBox">
      <Button variant="outline-info" onClick={()=>{props.dispatch({type: 'Payment'}); history.push(); }} >결제</Button>
      <Button variant="outline-info" onClick={()=>{props.dispatch({type: 'AllDelete'}); history.push("/"); }} >장바구니 비우기</Button>
      </div>
    </div>
  )
}
  

function dataBinding(state) {
  return {  
    state : state
    // state : state.reducer // 리듀서가 여러개일때
  }
}

export default connect(dataBinding)(Cart);
// export default Cart;