import { Button, Navbar,Nav, NavDropdown,FormControl, Form, Jumbotron, Spinner } from 'react-bootstrap';
import React, {useState, useContext } from 'react';
import './App.css';
import Data from "./Data.js";
import Detail from "./Detail.js";
import Cart from './Cart.js';
import Wait from './Wait.js';
import {Link, Route, Switch, useHistory} from 'react-router-dom';
import styled from 'styled-components';
import axios from 'axios';
import panelImg from './MainPanel.jpg';


let Word = styled.div`
    font-size : 25px;
    font-weight : ${ props => props.Weight};
`;

// let StockContext = React.createContext();
export let StockContext = React.createContext();

function App() {
    let history = useHistory();
    let [data, dataSet] = useState(Data);
    let [stock, stockSet] = useState([3,12,13,4,14,15]);
    let [dataZero, dataZeroSet ] = useState(false);
    let [spinnerOn, spinnerOnSet ] = useState(false);
    
    return (
        <div className="App">
            <Navbar bg="light" expand="lg">
                <Navbar.Brand onClick={()=>{history.push("/")}}>React-Bootstrap</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav"/>
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="ml-auto">
                        <Nav.Link onClick={()=>{history.push("/")}}>Home</Nav.Link>
                        <Nav.Link onClick={()=>{history.push("/cart")}}>장바구니</Nav.Link>
                        <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                            <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                            <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                            <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                            <NavDropdown.Divider/>
                            <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                        </NavDropdown>
                    </Nav>
                        <Form inline="inline">
                            <FormControl type="text" placeholder="Search" className="mr=l-sm-2"/>
                            <Button variant="outline-success">Search</Button>
                        </Form>
                </Navbar.Collapse>
            </Navbar>
            
            <Switch>
            <Route exact path="/">  {/* //메인페이지 */}
                <Jumbotron className="AdPanel">
                    {/* <img src={panelImg}></img> */}
                    <h1>20% Season Off</h1>
                    <p>
                        This is a simple hero unit, a simple jumbotron-style component for calling extra
                        attention to featured content or information.
                    </p>
                    <p>
                        <Button variant="primary">Learn more</Button>
                    </p>
                </Jumbotron>
                <div className="Container">
                    <StockContext.Provider value={stock}>
                    <div className="Row">{
                        // <div className="col-4">
                        //     <img src={ data[0].img }/>
                        //     <h4>{ data[0].title }</h4>
                        //     <p>{ data[0].content }<br/>{ data[0].price }원</p>
                        // </div>
                        data.map(function(e, i){
                            return(
                                <Items Data = { e } Num={i} key ={i}/>                                
                                // <Items Data = { data[i] } />
                            );
                        })
                    }</div>
                    </StockContext.Provider>
                    {spinnerOn === true ?
                        <Spinner animation="border" role="status">
                            <span className="sr-only">Loading...</span>
                        </Spinner>
                        : null
                    }
                    {dataZero === false ?
                    <button className="btn btn-primary" onClick={()=>{
                        spinnerOnSet(true);
                        let Count=0;
                        axios.get('https://codingapple1.github.io/shop/data2.json')
                        .then((result)=>{
                            // dataSet([...data, ...result.data]);
                            let newArray=[...data];
                            result.data.map(function(e, i){
                                newArray.push(e);
                            })
                            dataSet(newArray);
                            spinnerOnSet(false);
                            dataZeroSet(true);
                        })
                        .catch(()=>{
                            spinnerOnSet(false);
                            console.log('데이터 통신 실패');
                        });
                    }}>더보기</button>
                :null}
                </div>
            </Route>

            <Route path="/detail/:id">{/* 상세페이지 */}
                <StockContext.Provider value={stock}>
                    <Word Weight="bold">상세페이지</Word>
                    <Detail data={data} stock={stock} stockSet={stockSet}/>
                </StockContext.Provider>
            </Route>

            <Route path="/cart"> {/* 장바구니 */}
                <Cart></Cart>
            </Route>

            <Route path="/wait"> {/* 갱신용페이지 */}
                <Wait></Wait>
            </Route>

            <Route path="/:id">
            <Detail/>
            </Route> 

            </Switch>
        </div>
    );

    function Items(props){
        return(
            <div className="col-4">
                <Link to={"/detail/"+props.Num} >
                <div>
                <img src={"https://codingapple1.github.io/shop/shoes"+(Number([props.Num])+1)+".jpg"}/>
                </div>
                <h5>{ props.Data.title }</h5>
                <p>{props.Data.content}</p><p className="BeforePrice">{props.Data.price.toLocaleString()}원</p><p className="AfterPrice">{(props.Data.price*0.8).toLocaleString()}원</p>
                </Link>
                <Test Num={props.Num}></Test>
            </div>
        )
    }
    function Test(props){
        let stock2 = useContext(StockContext);
        return <p>{"수량 : "+stock2[props.Num]}</p>
        
    }
}

export default App;
